  mounted(){
    this.sendMessage(1)
  },
  methods: {
    async sendMessage(idx) {
      const msg = this.message;
      this.message =""
      const temp ={role:'user', content:msg}
      if(idx != 1){
        this.chatHistory.push(temp)
      }  
      try {
        const res = await axios.post('http://10.104.20.102:8082/send-message', {
          message: msg
        });
        this.response = res.data.response;
      } catch (error) {
        console.error('There was an error!', error);
      }
      this.message ="";
      const temp2 ={role:'assistant', content:this.response}
      this.chatHistory.push(temp2)     
    }
  }
};
