<template>
  <div id="body1">
    <div id="App" style="margin-bottom:10px">
      <h1>Welcome to Time Travel Restaurant</h1>
      <form @submit.prevent="calculateTotal">
        <section v-for="(menu, era,index1) in menus" :key="era">
          <h2>{{ era }}</h2>
          <div v-for="(item, index2) in menu" :key="item.name" class="menu-item">
            <input type="checkbox" :id="(index2+1)+(index1*5)" :value="item.price" v-model="selectedItems" @change="checkSelected(index1,index2,$event)">
            <label :for="item.name">{{ item.name }} - RMB {{ item.price }}</label>
            <button id="button_show" @click.prevent="change_status(index1,index2)">{{ !Show[(index2+1)+(index1*5)] ? "Show":"Hide" }}</button>
            <div v-if="item_number[(index2+1)+(index1*5)] != 0">
              <button id="button_add" @click.prevent="add_number(index1,index2)">+</button>
              <button id="button_minus" @click.prevent="minus_number(index1,index2)">-</button>
            </div>
            <span><strong>Number: </strong>{{ item_number[(index2+1)+(index1*5)] }}</span>
            <div v-if="Show[(index2+1)+(index1*5)]" class="imageContainer">
              <img :src="imagen(index1,index2)" alt="">
            </div>
          </div>
        </section>
        <button type="submit">Submit Order</button>
      </form>
      <h3 v-if="totalPrice > 0">Total Price: RMB {{ totalPrice }}</h3>
    </div>
  </div>
</template>
  
  <script>
  export default {
    data() {
      return {
        Show:[],
        menus: {
          "Caveman Café": [
            { name: "Roasted Wild Boar Ribs", price: 298},
            { name: "Smoked Salmon Slices", price: 258 },
            {name: "Stone Ground Corn Tortillas", price: 128},
            {name: "Game and Wild Vegetable Soup", price: 188},
            {name: "Honey Nuts", price: 98}
            // Add other items similarly...
          ],
          "Roman Bistro": [
            {name: "Roast Suckling Pig With Honey Wine", price:388 },
            {name: "Olive Broccoli", price: 168},
            {name: "Garlic Bread", price: 68},
            {name: "Roman Seafood Risotto", price: 328},
            {name: "Honey Almond Cake", price:58},
          ],
          "Renaissance Tavern": [
            {name:"Herb roast chicken", price:288},
            {name: "Mushroom Risotto", price: 198},
            {name: "Italian Tomato Beef", price:268},
            {name: "Grilled Lamb Chops With Rosemary", price: 327},
            {name: "Almond Meringue", price: 48}
          ],
          "Victorian Tea Room": [
            {name: "Victorian Scones", price: 88},
            {name: "Queen Victoria Cake", price: 127},
            {name: "English Three-tier Afternoon Tea", price:371},
            {name: "Victorian Scones", price:97},
            {name: "Royal Milk Tea", price:45},
          ],
          "Futuristic Diner": [
            {name: "Liquid Nitrogen Ice Cream", price:120},
            {name: "3D Printed Chocolate", price: 96},
            {name: "Artificial Meat Burger", price: 195},
            {name: "Transparent Sushi", price: 287},
            {name: "Energy Bar", price: 67}
          ],

          // Continue defining other eras and their meals similarly...
        },
        selectedItems: [],
        totalPrice: 0,
        item_number:[],
        prices: [0,298,258,128,188,98,388,168,68,328,58,288,198,268,327,48,88,127,371,97,45,120,96,195,287,67]
      };
    },
    mounted(){
      for(let i=0; i<=25;i++){
        this.Show.push(false)
        this.item_number.push(0)
      }
    },
    methods: {
      calculateTotal() {
        for(let i=1;i<=25;i++){
          this.totalPrice = this.totalPrice+ (this.item_number[i]* this.prices[i]);
          console.log(this.item_number[i]* this.prices[i])
        }
        console.log(this.totalPrice)
        console.log(this.item_number)
        console.log(this.prices)
        const message = "Total Price: " + this.totalPrice
        alert(message)
        console.log(this.selectedItems)
      },
      change_status(ind1,ind2){
        this.Show[(ind2+1)+(ind1*5)] = !this.Show[(ind2+1)+(ind1*5)]
      },
      imagen(ind1, ind2){
        const num = (ind2+1)+(ind1*5)
        return require(`@/assets/foods/${num}.png`)
      },
      add_number(ind1,ind2){
        const num = (ind2+1)+(ind1*5)
        this.item_number[num] += 1;
      },
      minus_number(ind1,ind2){
        const num = (ind2+1)+(ind1*5)
        if(this.item_number[num] === 1){
          alert("Please unchecked the box first")
        }
        this.item_number[num] = this.item_number[num]===1 ? 1:this.item_number[num]-1 ;       
      },
      checkSelected(ind1,ind2,event){
        const num = (ind2+1)+(ind1*5)
        if(event.target.checked){
          this.item_number[num] +=1;
          event.target.checked = true;
        }else{
          this.item_number[num] = 0;
          event.target.checked = false;
        }
      }
    },
  };
  </script>
  
  <style>
