    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #ff5400;
  }
#App {
  /* 初始状态 */
  opacity: 0; 
  transform: translateY(50px); /* 元素开始时在它最终位置的下面50px */
  /* 定义过渡效果 */
  animation: floatUp 0.5s forwards;
  animation-delay: 0.5s; /* 可选，如果你希望元素加载几分之一秒后再开始浮出来 */
}
