  width: 100px;
  height: auto;
  object-fit: cover;
}

#button_show{
  background-color: gray;
  font-size: 10px;
  color: white;
  margin-left: 10px;
  padding: 3px;
  border-radius: 1px;;
  margin-right: 10px;

}
#button_add, #button_minus{
  width: 10px;
  height: 10px;
  background-color: black;
  font-size: 10px;
  color: white;
  margin-right: 5px;
  padding: 0;
  border-radius: 0;
}

      
/* 定义关键帧 */
@keyframes floatUp {
  from {
    opacity: 0;
    transform: translateY(100px); /* 开始状态 */
  }
  to {
    opacity: 1;
    transform: translateY(0); /* 结束状态，即元素回到原位 */
  }
