import { createApp } from 'vue'
import { createMemoryHistory, createRouter } from 'vue-router'
import App from './App.vue'
import orderpage from './components/OrderPage.vue'
import homepage from './components/HomePage.vue'
import kimiAiService from './components/kimiAIPage.vue'
import HistoryName from './components/HistoryName.vue'

const routes=[
    {path: '/HomePage', component: homepage},
    {path: '/OrderPage', component: orderpage},
    {path: '/kimiAiService', component: kimiAiService},
    {path: '/ActivityPage', component: HistoryName}
]
const router= createRouter({
    history: createMemoryHistory(),
    routes
})
createApp(App).use(router).mount('#app')
